*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

button{
  font-size: 1.3rem;
  margin-right: 10px;
  padding: 10px;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid red;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 240px;
  
}